<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> 152 товара </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :product-color.sync="filterColorId"
      />

      <section class="catalog">
        <ProductsLoadingFaled v-if="productsLoadingFaled" :action="loadProducts" btn-title="Попробовать снова" />
        <ProductsLoading v-if="productsLoading" />

        <!-- <div v-if="productsLoadingFaled">
          Произошла ошибка при загрузке товаров
          <button @click.prevent="loadProducts">Попробовать еще раз</button>
        </div> -->

        <ProductList :products="products" />

        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue'
import BasePagination from '@/components/BasePagination.vue'
import ProductFilter from '@/components/ProductFilter.vue'
import axios from 'axios'
import { API_BASE_URL } from '@/config'
import ProductsLoading from '@/components/ProductsLoading.vue'
import ProductsLoadingFaled from '@/components/ProductsLoadingFaled.vue'

export default {
  components: { ProductList, BasePagination, ProductFilter, ProductsLoading, ProductsLoadingFaled },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,

      page: 1,
      productsPerPage: 3,

      productsData: null,

      productsLoading: false,
      productsLoadingFaled: false
    }
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({
            ...product,
            img: product.image.file.url
          }))
        : []
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0
    }
  },
  watch: {
    page() {
      this.loadProducts()
    },
    filterPriceTo() {
      this.loadProducts()
    },
    filterCategoryId() {
      this.loadProducts()
    },
    filterPriceFrom() {
      this.loadProducts()
    },
    filterColorId() {
      this.loadProducts()
    }
  },
  methods: {
    loadProducts() {
      this.productsLoading = true
      this.productsLoadingFaled = false
      clearTimeout(this.loadProductsTimer)
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL  }/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColorId
            }
          })
          .then((response) => {
            this.productsData = response.data
          })
          .catch(() => {
            this.productsLoadingFaled = true
          })
          .then(() => {
            this.productsLoading = false
          })
      }, 500)
    }
  },
  created() {
    this.loadProducts()
  }
}
</script>
