<template>
  <ProductsLoading v-if="loadingCart" class="full-screen-height" />
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info">
        {{ products.length | pluralizeProductCount }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе</p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" :to="{ name: 'order' }" class="cart__button button button--primery" type="submit"
            >Оформить заказ</router-link
          >
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat'
import pluralize from '@/helpers/pluralize'
import { mapGetters } from 'vuex'
import CartItem from '@/components/CartItem.vue'
import ProductsLoading from '@/components/ProductsLoading.vue'
// eslint-disable-next-line no-unused-vars
import products from '@/data/products'

export default {
  data() {
    return {
      loadingCart: false,
      productAmount: 1
    }
  },
  filters: {
    numberFormat,
    pluralizeProductCount: (count) => `${count} ${pluralize(count, ['товар', 'товара', 'товаров'])}`
  },
  components: { CartItem, ProductsLoading },
  computed: {
    ...mapGetters({ products: 'cartDetailsProducts', totalPrice: 'cartTotalPrice' })
  },
  watch: {
    '$store.state.loadingCart': {
      handler() {
        this.loadingCart = this.$store.state.loadingCart
      },
      immediate: true
    }
  }
}
</script>
